.editor * {
  z-index: 999;
}

.editor-enter {
  background-color: rgba(0, 0, 0, 0);
  -webkit-backdrop-filter: blur(0);
  backdrop-filter: blur(0);
}

.editor-enter-active {
  background-color: rgba(0, 0, 0, 0.9);
  -webkit-backdrop-filter: blur(0.125rem);
  backdrop-filter: blur(0.125rem);
  -webkit-transition: background-color 0.25s, -webkit-backdrop-filter 0.25s;
  transition: background-color 0.25s, -webkit-backdrop-filter 0.25s;
  -o-transition: background-color 0.25s, backdrop-filter 0.25s;
  transition: background-color 0.25s, backdrop-filter 0.25s;
  transition: background-color 0.25s, backdrop-filter 0.25s, -webkit-backdrop-filter 0.25s;
}

.editor-leave {
  background-color: rgba(0, 0, 0, 0.9);
  -webkit-backdrop-filter: blur(0.125rem);
  backdrop-filter: blur(0.125rem);
}

.editor-leave-active {
  background-color: rgba(0, 0, 0, 0.0);
  -webkit-backdrop-filter: blur(0);
  backdrop-filter: blur(0);
  -webkit-transition: background-color 0.25s, -webkit-backdrop-filter 0.25s;
  transition: background-color 0.25s, -webkit-backdrop-filter 0.25s;
  -o-transition: background-color 0.25s, backdrop-filter 0.25s;
  transition: background-color 0.25s, backdrop-filter 0.25s;
  transition: background-color 0.25s, backdrop-filter 0.25s, -webkit-backdrop-filter 0.25s;
}

svg { display: block; }

/* For draft-js */

figure {
  margin: 0 !important;
  padding: 0 !important;
  display: block !important;
}



